  /*
    {{#let
    (or @nestedLevel 1)
    (eq @centerStatusId @conversation.status.id)
    (get (service "status-actions") "CONVERSATION_MAX_DEPTH")
    as |nestedLevel isActiveStatus maxDepth|
  }}
    {{#if @conversation.tombstone}}
      <article class="feed-item__threaded feed-item tombstone">
        <div class="feed-item__avatar">
          <Avatar
            @tombstone={{true}}
          />
        </div>
        <div class="feed-item__header tombstone">
          {{t "statusUnavailable"}}
        </div>
      </article>
    {{else}}
      <FeedItem
        data-test-status-level={{nestedLevel}}
        @feedItem={{@conversation.status}}
        @showMutedAvatars={{true}}
        data-is-active-item={{eq @centerStatusId @conversation.status.id}}
        {{class-list
          "feed-item__threaded"
          orphan=(eq @conversation.orphan true)
          conversation__parent=(if (eq nestedLevel 1) (and (gt @conversation.children.length 0)))
          feed-item--anchor=(if (eq nestedLevel 1) (and isActiveStatus (to-bool @conversation.children.length)) isActiveStatus)
        }}
        ...attributes
      />
    {{/if}}
  
    {{#if @conversation.children}}
      {{#if (or @conversation.containsCurrent isActiveStatus)}}
        {{!-- Dont occlude tree if contains active status --}}
        <div class="conversation__nest conversation__nest--{{nestedLevel}}">
          {{#each @conversation.children key="id" as |child|}}
            <Thread
              @conversation={{child}}
              @centerStatusId={{@centerStatusId}}
              @nestedLevel={{add nestedLevel 1}}
            />
          {{/each}}
        </div>
      {{else}}
        {{#if (lt nestedLevel (sub maxDepth 1))}}
          {{!-- Below nesting level 5, occlude items after 5 --}}
          {{#let (take maxDepth @conversation.children) (drop maxDepth @conversation.children) (ember-object) as |visibleChildren remainingChildren scope|}}
            <div class="conversation__nest conversation__nest--{{nestedLevel}}">
              {{#each visibleChildren key="id" as |child|}}
                <Thread
                  @conversation={{child}}
                  @centerStatusId={{@centerStatusId}}
                  @nestedLevel={{add nestedLevel 1}}
                  class={{if remainingChildren.length "conversation__last-sibling-before-cut"}}
                />
              {{/each}}
  
              {{#if remainingChildren}}
                {{#if scope.expanded}}
                  {{#each remainingChildren key="id" as |child|}}
                    <Thread
                      @conversation={{child}}
                      @centerStatusId={{@centerStatusId}}
                      @nestedLevel={{add nestedLevel 1}}
                      class={{if remainingChildren.length "conversation__last-sibling-before-cut"}}
                    />
                  {{/each}}
                {{else}}
                  <div class="conversation__more-reply-siblings">
                    <button
                      type="button"
                      {{on "click" (toggle "expanded" scope)}}
                      data-test-selector="link-to-remaining"
                      class="conversation__more-replies-link"
                    >
                      {{t "numMoreReplies" n=remainingChildren.length}}
                    </button>
                  </div>
                {{/if}}
              {{/if}}
            </div>
          {{/let}}
        {{else}}
          {{!-- After Nesting level 5, occlude ALL items --}}
          <div class="conversation__more-reply-descendents">
            <LinkTo
              @route="status.details"
              @model={{@conversation.status.id}}
              data-test-selector="child-replies-hidden"
              class="conversation__more-replies-link"
            >
              {{t "numMoreReplies" n=@conversation.children.length}}
            </LinkTo>
          </div>
        {{/if}}
      {{/if}}
    {{/if}}
  {{/let}}
  
  */
