  /*
    <div ...attributes>
    {{#each @toasts as |toast|}}
      {{#if (eq toast.type "newStatus")}}
        <div class="notification notification--success" data-test-toast-type="new-status">
          <span class="notification__message" data-test-selector="toast-message">{{t "yourStatusWasPosted"}}</span>
          <LinkTo
            class="notification__button"
            data-test-selector="new-status-toast-link"
            @route="status.details"
            @model={{toast.payload.newStatus.id}}
          >
            {{t "view"}}
          </LinkTo>
        </div>
      {{else if (eq toast.type "newReply")}}
        <div class="notification notification--success" data-test-toast-type="new-reply">
          <span class="notification__message" data-test-selector="toast-message">{{t "yourReplyWasPosted"}}</span>
          <LinkTo
            class="notification__button"
            data-test-selector="new-reply-toast-link"
            @route="status.details"
            @model={{toast.payload.newStatus.id}}
          >
            {{t "view"}}
          </LinkTo>
        </div>
      {{else if (eq toast.type "userBlocked")}}
        <div class="notification notification--success" data-test-toast-type="user-blocked">
          <span class="notification__message" data-test-selector="toast-message">{{t "userBlocked" username=toast.payload.user.username}}</span>
          <button
            type="button"
            class="notification__button"
            data-test-selector="user-blocked-unblock-button"
            {{on "click" (perform (get (service "user-actions") "unblockUser") toast.payload.user)}}
          >
            {{t "unblock"}}
          </button>
        </div>
      {{else if (eq toast.type "userUnblocked")}}
        <div class="notification notification--success" data-test-toast-type="user-unblocked">
          <span class="notification__message" data-test-selector="toast-message">{{t "userUnblocked" username=toast.payload.user.username}}</span>
          <button
            type="button"
            class="notification__button"
            data-test-selector="user-unblocked-block-button"
            {{on "click" (perform (get (service "user-actions") "blockUser") toast.payload.user)}}
          >
            {{t "block"}}
          </button>
        </div>
      {{else if (eq toast.type "userMuted")}}
        <div class="notification notification--success" data-test-toast-type="user-muted">
          <span class="notification__message" data-test-selector="toast-message">{{t "userMuted" username=toast.payload.user.username}}</span>
          <button
            type="button"
            class="notification__button"
            data-test-selector="user-muted-unmute-button"
            {{on "click" (perform (get (service "user-actions") "unmuteUser") toast.payload.user)}}
          >
            {{t "unmute"}}
          </button>
        </div>
      {{else if (eq toast.type "userUnmuted")}}
        <div class="notification notification--success" data-test-toast-type="user-unmuted">
          <span class="notification__message" data-test-selector="toast-message">{{t "userUnmuted" username=toast.payload.user.username}}</span>
          <button
            type="button"
            class="notification__button"
            data-test-selector="user-unmuted-mute-button"
            {{on "click" (perform (get (service "user-actions") "muteUser") toast.payload.user)}}
          >
            {{t "mute"}}
          </button>
        </div>
      {{else if (eq toast.type "userFollowed")}}
        <div class="notification notification--success" data-test-toast-type="user-followed">
          <span class="notification__message" data-test-selector="toast-message">{{t "nowFollowing" username=toast.payload.user.username}}</span>
          <button
            type="button"
            class="notification__button"
            data-test-selector="user-followed-unfollow-button"
            {{on "click" (perform (get (service "user-actions") "unfollowUser") toast.payload.user)}}
          >
            {{t "unfollow"}}
          </button>
        </div>
      {{else if (eq toast.type "userUnfollowed")}}
        <div class="notification notification--success" data-test-toast-type="user-unfollowed">
          <span class="notification__message" data-test-selector="toast-message">{{t "userUnfollowed" username=toast.payload.user.username}}</span>
          <button
            type="button"
            class="notification__button"
            data-test-selector="user-unfollowed-follow-button"
            {{on "click" (perform (get (service "user-actions") "followUser") toast.payload.user)}}
          >
            {{t "follow"}}
          </button>
        </div>
      {{else if (eq toast.type "conversationMuted")}}
        <div class="notification notification--success" data-test-toast-type="conversation-muted">
          <span class="notification__message" data-test-selector="toast-message">{{t "conversationMuted"}}</span>
          <button
            type="button"
            class="notification__button"
            data-test-selector="conversation-muted-unmute-button"
            {{on "click" (perform (get (service "status-actions") "unmuteConversation") toast.payload.status)}}
          >
            {{t "unmute"}}
          </button>
        </div>
      {{else if (eq toast.type "conversationUnmuted")}}
        <div class="notification notification--success" data-test-toast-type="conversation-unmuted">
          <span class="notification__message" data-test-selector="toast-message">{{t "conversationUnmuted"}}</span>
          <button
            type="button"
            class="notification__button"
            data-test-selector="conversation-unmuted-mute-button"
            {{on "click" (perform (get (service "status-actions") "muteConversation") toast.payload.status)}}
          >
            {{t "mute"}}
          </button>
        </div>
      {{else if (eq toast.type "statusReposted")}}
        <div class="notification notification--success" data-test-toast-type="status-reposted">
          <span class="notification__message" data-test-selector="toast-message">{{t "statusReposted"}}</span>
          <LinkTo
            class="notification__button"
            data-test-selector="status-reposted-toast-link"
            @route="status.details"
            @model={{toast.payload.newStatus.id}}
          >
            {{t "view"}}
          </LinkTo>
        </div>
      {{else if (eq toast.type "error")}}
        <div class="notification notification--error" data-test-toast-type="error">
          <div class="notification__content">
            <span class="notification__message" data-test-selector="toast-message">{{toast.payload.message}}</span>
            <button
              type="button"
              class="notification__button"
              data-test-selector="dismiss-toast-button"
              {{on "click" (fn (get (service "toast") "dismiss") toast.id)}}
              title={{t "dismissToast"}}
            >
              {{svg-jar "close-circle"}}
            </button>
          </div>
        </div>
      {{else if (eq toast.type "dismissibleMessage")}}
        <div class="notification notification--dismissible-message" data-test-toast-type="dismissible-message">
          {{!-- template-lint-disable no-invalid-interactive --}}
          <div
            class="notification__content"
            {{on "click" (transition-to toast.payload.link)}}
          >
            <span class="notification__message" data-test-selector="toast-message">{{toast.payload.message}}</span>
  
            {{#if toast.payload.learnMore}}
              <span class="notification__link">{{toast.payload.learnMore}}</span>
            {{/if}}
  
            <button
              type="button"
              class="notification__button"
              data-test-selector="dismiss-toast-button"
              {{on "click" (pipe
                (stop-propagation)
                (prevent-default)
                (fn (get (service "toast") "dismiss") toast.id)
              )}}
              title={{t "dismissToast"}}
            >
              {{svg-jar "close-circle"}}
            </button>
          </div>
          {{!-- template-lint-enable no-invalid-interactive --}}
        </div>
      {{else}}
        <div class="notification notification--{{toast.type}} notification--noaction" data-test-toast-type={{toast.type}}>
          <span class="notification__message" data-test-selector="toast-message">{{toast.payload.message}}</span>
        </div>
      {{/if}}
    {{/each}}
  </div>
  
  */
