  /*
    {{#if @currentTab}}
    {{#let (service "authModal") (service "instances") as |authModal instances|}}
      <BasicDropdown
        @initiallyOpened={{true}}
        @onClose={{authModal.close}}
        as |dd|
      >
        <dd.Content
          @overlay={{true}}
          data-test-selector="auth-modal"
        >
          <div {{lock-body-scroll enabled=true}} data-test-selector="lock-body-scroll"></div>
  
          <div class="modal-form modal-form--sign-in sign-in-form sign-in-form--modal">
            <div class="modal-form__container">
              <div class="modal-form__dialog">
                <div class="modal-form__dialog-content">
                  <FocusTrap>
                    <nav class="modal-form__nav">
                      {{#if (not (contains @currentTab (array "sign-in" "forgot-password-success" "onboarding-avatar-form")))}}
                        {{#if (eq @currentTab "onboarding-bio-form")}}
                          <button type="button" {{on "click" (stop-propagation authModal.showOnboardingAvatarForm)}} data-test-selector="auth-modal-back-button">{{svg-jar "back-arrow"}}</button>
                        {{else}}
                          <button type="button" {{on "click" (stop-propagation authModal.showSignInModal)}} data-test-selector="auth-modal-back-button">{{svg-jar "back-arrow"}}</button>
                        {{/if}}
                      {{/if}}
  
                      <button type="button" class="modal-form__close" title={{t "close"}} {{on "click" (stop-propagation dd.actions.close)}} data-test-selector="close-modal-button">{{svg-jar "close"}}</button>
                    </nav>
  
                    {{#if (eq @currentTab "sign-in")}}
                      <AuthModal::SignInForm />
                    {{else if (eq @currentTab "register")}}
                      {{#if (feature-flag "registration")}}
                        <AuthModal::RegistrationForm
                          @blockExit={{authModal.blockExit}}
                          @allowExit={{authModal.allowExit}}
                          @registerUser={{get (service "user-actions") "registerUser"}}
                          @onRegistered={{authModal.showOnboardingAvatarForm}}
                        />
                      {{else}}
                        {{t "featureNotEnabled" feature="Registration"}}
                      {{/if}}
                    {{else if (eq @currentTab "forgot-password")}}
                      {{#if instances.current.nodeInfo.metadata.mailerEnabled}}
                        <AuthModal::ForgotPasswordForm />
                      {{else}}
                        <div data-test-selector="forgot-password-not-enabled-callout">
                          {{t "resetPasswordNotEnabled"}}
                        </div>
                      {{/if}}
                    {{else if (eq @currentTab "forgot-password-success")}}
                      <AuthModal::ForgotPasswordSuccess />
                    {{else if (eq @currentTab "onboarding-bio-form")}}
                      <AuthModal::Onboarding::BioForm
                        @updateUserBio={{get (service "user-actions") "updateUser"}}
                        @onClose={{dd.actions.close}}
                      />
                    {{else if (eq @currentTab "onboarding-avatar-form")}}
                      <AuthModal::Onboarding::AvatarForm
                        @updateAvatar={{get (service "userActions") "setAvatar"}}
                        @clearAvatar={{get (service "userActions") "clearAvatar"}}
                      />
                    {{/if}}
                  </FocusTrap>
                </div>
              </div>
            </div>
          </div>
  
          <OnEscapeKeyPressed @handler={{dd.actions.close}}/>
        </dd.Content>
      </BasicDropdown>
    {{/let}}
  {{/if}}
  
  */
